.banner{
    object-fit: contain;
    height: 500px;

 
}

.banner__content {
    font-size: 15px;
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title  {
    font-size: 3rem;
    font-weight:  800;
    padding-bottom: 0.3rem;
    font-family: 'Raleway', sans-serif;
        

}

.description{
    font-family: 'Kalam', cursive;
    font-size: 1.2rem;
    padding-top: 1.2rem;
    width: 20rem;
    line-height: 1.3;
    max-width: 360px;
    height: 80px;

}


.banner_btn {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgb(47, 47, 47);
}

.banner_btn:hover {
    color: #000;
    background-color: #fff;
    transform: all 0.2s;
}

.banner--fade{
    height: 19.5rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61), #111
    );
}