.nav{
    display: flex;
    position: fixed;
    height: 100px;
    z-index: 1;
    top: 0;
    width: 100%;
    padding: 20px;
    justify-content: space-between;
    transition-timing-function: ease-in;
    transition: all 0.6s;
}

.nav__black{
    background-color: #000;
}


.nav__logo{
    color: red;
    margin-left: 15px;
    /* font-family: 'Kalam', cursive; */
    left: 20px;
    object-fit: contain;
    width: 80px;
   

}

.logo_avatar{
    height: 30px;
    width: 30px;
    object-fit: contain;
}