.row__poster{
    width: 100%;
    object-fit: contain;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
}

.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}


.row__poster:hover {
    transform: scale(1.08);
    opacity: 1;
}

.row__posters::-webkit-scrollbar {
    display: none;
}

.row__large{
    max-height: 250px;
}

.row__large:hover {
    transform: scale(1.09);
    opacity: 1;
}

.row{
    margin-left: 20px;
}

.row h2{
    /* background: linear-gradient(90deg, #00dbde, #fc00ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; */
    background-image: linear-gradient(90deg, #00dbde, 5%, #16d2a6, 6%, #fc00ff 20%);
    -webkit-background-clip: text;
    -webkit-text-fill-color:  transparent;

}